<template>
    <div id="apply">
        <div class="apply-row">
            <p></p>
            <div>
                <Tabs value="name1" :animated="false">
                    <TabPane label="" name="name1"></TabPane>
                    <TabPane label="" name="name2"></TabPane>
                    <TabPane label="" name="name3"></TabPane>
                </Tabs>
                <Tabs type="card" :animated="false">
                    <TabPane label="" name="name1"></TabPane>
                    <TabPane label="" name="name2"></TabPane>
                    <TabPane label="" name="name3"></TabPane>
                </Tabs>
            </div>
        </div>
        <div class="apply-row">
            <p></p>
            <div style="flex-direction: column">
                <Page :total="10" show-total show-elevator/>
                <Page :total="100" show-total show-elevator/>
                <Page :total="100" show-total size="small" show-elevator/>
            </div>
        </div>
        <div class="apply-row">
            <p></p>
            <div style="flex-direction: column">
                <Breadcrumb separator=">">
                    <BreadcrumbItem></BreadcrumbItem>
                    <BreadcrumbItem></BreadcrumbItem>
                    <BreadcrumbItem></BreadcrumbItem>
                </Breadcrumb>
                <Breadcrumb>
                    <BreadcrumbItem></BreadcrumbItem>
                    <BreadcrumbItem></BreadcrumbItem>
                    <BreadcrumbItem></BreadcrumbItem>
                </Breadcrumb>
                <Breadcrumb separator=">">
                    <BreadcrumbItem>
                        <Icon type="ios-home-outline"></Icon>
                        
                    </BreadcrumbItem>
                    <BreadcrumbItem>
                        <Icon type="logo-buffer"></Icon>
                        
                    </BreadcrumbItem>
                    <BreadcrumbItem>
                        <Icon type="logo-buffer"></Icon>
                        
                    </BreadcrumbItem>
                </Breadcrumb>
            </div>
        </div>
        <div class="apply-row">
            <p></p>
            <div style="flex-direction: column;height: auto">
                <Steps :current="1" style="width: 80%;margin-bottom: 20px">
                    <Step title="" content=""></Step>
                    <Step title="" content=""></Step>
                    <Step title="" content=""></Step>
                    <Step title="" content=""></Step>
                </Steps>
                <Steps :current="1" status="error" style="width: 80%;margin-bottom: 20px">
                    <Step title="" content=""></Step>
                    <Step title="" content=""></Step>
                    <Step title="" content=""></Step>
                    <Step title="" content=""></Step>
                </Steps>
                <div style="display: flex;width: 80%;">
                    <Steps :current="2" direction="vertical" style="width: 45%;">
                        <Step title="" content=""></Step>
                        <Step title="" content=""></Step>
                        <Step title="" content=""></Step>
                        <Step title="" content=""></Step>
                    </Steps>
                    <Timeline>
                        <TimelineItem color="green">
                            <p class="time">1976</p>
                            <p class="content">Apple I </p>
                        </TimelineItem>
                        <TimelineItem color="green">
                            <p class="time">1984</p>
                            <p class="content"> Macintosh</p>
                        </TimelineItem>
                        <TimelineItem color="red">
                            <p class="time">2007</p>
                            <p class="content"> iPhone</p>
                        </TimelineItem>
                        <TimelineItem color="blue">
                            <p class="time">2010</p>
                            <p class="content"> iPad</p>
                        </TimelineItem>
                    </Timeline>
                </div>
            </div>
        </div>
        <div class="apply-row">
            <p></p>
            <div style="height: auto">
                <iViewTwo></iViewTwo>
            </div>
        </div>
        <div class="apply-row">
            <p></p>
            <div>
                <div style="width: 40%;">
                    <Button @click="background('info',false)" class="margin-10"></Button>
                    <Button @click="background('info')" class="margin-10"></Button>
                    <Button @click="background('success')" class="margin-10"></Button>
                    <Button @click="background('warning')" class="margin-10"></Button>
                    <Button @click="background('error')" class="margin-10"></Button>
                </div>
                <div style="width: 30%;">
                    <Button @click="openConfirm('del')" class="margin-10"></Button>
                </div>
                <div style="width: 30%;">
                    <Button @click="show = true" class="margin-10"></Button>
                </div>
            </div>
        </div>
        <div class="apply-row">
            <p></p>
            <div>
                <div style="width: 45%;">
                    <Button @click="value1 = true" class="margin-10"></Button>
                </div>
                <div style="width: 45%;">
                    <Button @click="value2 = true" class="margin-10"></Button>
                </div>
            </div>
        </div>
        <div class="apply-row">
            <p></p>
            <div style="height: 400px;">
                <CwTable style="width: 100%;"></CwTable>
            </div>
        </div>
        <div class="apply-row">
            <p></p>
            <div style="height: 400px;">
                <div id="panChartLeft" :style="{width: '100%', height: '350px'}"></div>
            </div>
        </div>
        <Modal v-model="show"
               width="420"
               title=""
               :mask-closable="false">
            <Form ref="forms" :model="formItem" :label-width="80">
                <FormItem label=""
                          prop="input"
                          :rules="{required: true, message: '', trigger: 'blur'}">
                    <Input v-model="formItem.input" placeholder=""></Input>
                </FormItem>
                <FormItem label="">
                    <Select v-model="formItem.select" v-validate="'required'"
                            name="">
                        <Option value="beijing">New York</Option>
                        <Option value="shanghai">London</Option>
                        <Option value="shenzhen">Sydney</Option>
                    </Select>
                </FormItem>
            </Form>
<!--            <div slot="footer">-->
<!--                <Button class="margin-left10"></Button>-->
<!--                <Button type="primary"-->
<!--                        @click="MountingOK()"-->
<!--                >-->
<!--                    -->
<!--                </Button>-->
<!--            </div>-->
        </Modal>
        <Drawer title="Basic Drawer" :closable="false" v-model="value1" width="600">
            <p>Some contents...</p>
            <p>Some contents...</p>
            <p>Some contents...</p>
        </Drawer>
        <Drawer title="Basic Drawer" :mask-closable="false" :closable="false" v-model="value2" width="600">
            <Form ref="forms" :model="formItem" :label-width="80">
                <FormItem label=""
                          prop="input"
                          :rules="{required: true, message: '', trigger: 'blur'}">
                    <Input v-model="formItem.input" placeholder="" style="width: 320px;"/>
                </FormItem>
                <FormItem label="">
                    <Select v-model="formItem.select" v-validate="'required'"
                            style="width: 320px;" name="">
                        <Option value="beijing">New York</Option>
                        <Option value="shanghai">London</Option>
                        <Option value="shenzhen">Sydney</Option>
                    </Select>
                </FormItem>
            </Form>
            <div class="demo-drawer-footer">
                <Button type="primary" @click="value2 = false"></Button>
                <Button style="margin-right: 8px" @click="value2 = false"></Button>
            </div>
        </Drawer>
    </div>
</template>

<script>
    import CwTable from '../../components/table/cw-table';

    export default {
        name: 'apply',
        components: {CwTable},
        data() {
            return {
                show: false,
                value1: false,
                value2: false,
                formItem: {}
            }
        },
        mounted() {
            this.drawLeft()
        },
        methods: {
            background(type, back = true) {
                this.$Message[type]({
                    background: back,
                    content: ''
                });
            },
            openConfirm(type) {
                this.$CWConfirm({
                    type: type,
                    title: '',
                    content: '',
                    okText: '',
                    cancelText: '',
                }).then(() => {
                    console.log('')
                }).catch(() => {
                    console.log('')
                });
            },
            drawLeft() {
                //
                let panChartLeft = this.$echarts.init(document.getElementById('panChartLeft'));
                panChartLeft.setOption({
                    title: {
                        text: '',
                    },
                    tooltip: {
                        trigger: 'axis',
                        backgroundColor: '#fff',
                        textStyle: {
                            color: '#5F6568' //
                        },
                        padding: 10,
                        extraCssText: 'box-shadow: 0 2px 6px rgba(0, 0, 0, 0.12);'
                    },
                    legend: {
                        itemHeight: 12,
                        itemWidth: 12,
                        icon: 'rect',
                        data: ['', '', ''],
                        x: 'right',
                        padding: [10, 20]
                    },
                    grid: {
                        x: 50,
                        y: 50,
                        x2: 20,
                        y2: 20
                    },
                    xAxis: [
                        {
                            type: 'category',
                            boundaryGap: false,
                            data: ['', '', '', '', '', '', '']
                        }
                    ],
                    yAxis: [
                        {
                            type: 'value'
                        }
                    ],
                    series: [
                        {
                            name: '',
                            type: 'line',
                            smooth: true,
                            itemStyle: {normal: {areaStyle: {type: 'default'}}},
                            data: [10, 12, 21, 54, 260, 830, 710]
                        },
                        {
                            name: '',
                            type: 'line',
                            smooth: true,
                            itemStyle: {normal: {areaStyle: {type: 'default'}}},
                            data: [30, 182, 434, 791, 390, 30, 10]
                        },
                        {
                            name: '',
                            type: 'line',
                            smooth: true,
                            itemStyle: {normal: {areaStyle: {type: 'default'}}},
                            data: [1320, 1132, 601, 234, 120, 90, 20]
                        }
                    ]
                });
            },
        }
    }
</script>

<style scoped lang="scss">
    #apply {
        overflow-y: auto;

        .time {
            font-size: 14px;
            font-weight: bold;
        }

        .content {
            padding-left: 5px;
        }

        .apply-row {
            margin-bottom: 20px;

            > > p {
                font-size: 24px;
                margin-bottom: 8px;
                font-weight: bold;
            }

            > > div {
                display: flex;
                justify-content: space-between;
                height: 120px;

                > > div {
                    width: 45%;
                }
            }
        }
    }

    .demo-drawer-footer {
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        border-top: 1px solid #e8e8e8;
        padding: 10px 16px;
        text-align: left;
        background: #fff;
    }
</style>



