<template>
    <div id="font">
        <div class="Titles font-20">Font </div>
        <div class="summary font-18"></div>
        <div class="summaryContent font-14">
            body
            reset8px12pxline-height20px
        </div>
        <div class="summaryContent font-16">
        </div>
        <div class="fontSty">
            <div class="chineseFont">
                <div class="chineseFont-title font-14">
                    <li></li>
                </div>
                <div class="chineseContent">
                    <div class="chineseContent-item">
                        <div class="mainFont PingFang"></div>
                        <div class="mainFontfamily PingFang">PingFang SC</div>
                        <div class="mainFontdesc PingFang"></div>
                    </div>
                    <div class="chineseContent-item">
                        <div class="mainFont Source"></div>
                        <div class="mainFontfamily Source">Source Han Sans</div>
                        <div class="mainFontdesc Source"></div>
                    </div>
                    <div class="chineseContent-item">
                        <div class="mainFont Microsoft"></div>
                        <div class="mainFontfamily Microsoft">Microsoft YaHei</div>
                        <div class="mainFontdesc Microsoft"></div>
                    </div>
                </div>
            </div>
            <div class="chineseFont">
                <div class="chineseFont-title font-14">
                    <li></li>
                </div>
                <div class="chineseContent">
                    <div class="chineseContent-item">
                        <div class="mainFont HelveticaNeue">Aa</div>
                        <div class="mainFontfamily HelveticaNeue">Helvetica Neue</div>
                        <div class="mainFontdesc HelveticaNeue"></div>
                    </div>
                    <div class="chineseContent-item">
                        <div class="mainFont Helvetica">Aa</div>
                        <div class="mainFontfamily Helvetica">Helvetica</div>
                        <div class="mainFontdesc Helvetica"></div>
                    </div>
                    <div class="chineseContent-item">
                        <div class="mainFont Arial">Aa</div>
                        <div class="mainFontfamily Arial">Arial</div>
                        <div class="mainFontdesc Arial"></div>
                    </div>
                </div>
            </div>
        </div>
        <div class="useSpecification">
            <div class="useSpecification-tips">
                <div class="useSpecification-tips-left font-16"></div>
                <div class="useSpecification-tips-right font-14">font-weight600</div>
            </div>
            <Table :columns="columns1" :data="data1"></Table>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'font',
        data() {
            return {
                columns1: [
                    {
                        title: '',
                        key: 'Hierarchy'
                    },
                    {
                        title: '',
                        key: 'fontsize'
                    },
                    {
                        title: '',
                        key: 'thickness'
                    },
                    {
                        title: '',
                        key: 'example',
                        render: (h, params) => {
                            let texts = ''; //
                            let fontcolor = '5F6568'; //
                            if (params.row.Hierarchy === '') {
                                texts = '32px'
                                fontcolor = '#282A2B'
                            } else if (params.row.Hierarchy === '') {
                                texts = '20px'
                                fontcolor = '#282A2B'
                            } else if (params.row.Hierarchy === '') {
                                texts = '18px'
                                fontcolor = '#282A2B'
                            } else if (params.row.Hierarchy === '') {
                                texts = '16px'
                                fontcolor = '#282A2B'
                            } else if (params.row.Hierarchy === '') {
                                texts = '14px'
                            } else if (params.row.Hierarchy === '()') {
                                texts = '13px'
                            } else if (params.row.Hierarchy === '') {
                                texts = '12px'
                                fontcolor = '#C5CACF'
                            }
                            return h('div', {
                                style: {
                                    fontSize: texts,
                                    color: fontcolor,
                                }
                            }, params.row.example);
                        }
                    }
                ],
                data1: [
                    {
                        'Hierarchy': '', 'fontsize': '32px', 'thickness': '/', 'example': ''
                    }, {
                        'Hierarchy': '', 'fontsize': '20px', 'thickness': '/', 'example': ''
                    }, {
                        'Hierarchy': '', 'fontsize': '18px', 'thickness': '/', 'example': ''
                    }, {
                        'Hierarchy': '', 'fontsize': '16px', 'thickness': '/', 'example': ''
                    }, {
                        'Hierarchy': '', 'fontsize': '14px', 'thickness': '', 'example': ''
                    }, {
                        'Hierarchy': '()', 'fontsize': '13px', 'thickness': '', 'example': ''
                    }, {
                        'Hierarchy': '', 'fontsize': '12px', 'thickness': '', 'example': ''
                    }
                ],
            }
        },
    }
</script>

<style scoped lang="scss">
    #font {
        width: 100%;
        height: 100%;
        overflow-y: auto;

        .Titles {
            height: 40px;
            width: 100%;
            line-height: 30px;
        }

        .summary {
            height: 40px;
            width: 100%;
            line-height: 20px;
            margin-top: 10px;
        }

        .summaryContent {
            height: 40px;
            width: 100%;
            margin-left: 5px;
            line-height: 20px;
            margin-top: 10px;
        }

        .fontSty {
            height: 60%;
            width: 100%;
            margin-left: 5px;
            line-height: 20px;
            margin-top: 10px;

            .chineseFont {
                height: 50%;
                width: 100%;

                .chineseFont-title {
                    width: calc(100% - 16px);
                    margin-left: 16px;
                    height: 30px;
                }

                .chineseContent {
                    height: calc(100% - 30px);
                    width: 100%;

                    .chineseContent-item {
                        width: calc(33.33% - 16px);
                        height: 100%;
                        float: left;
                        text-align: center;
                        margin-right: 16px;

                        .mainFont {
                            font-size: 56px;
                            height: 68px;
                            line-height: 68px;
                            color: #474c5a;
                        }

                        .mainFontfamily {
                            font-size: 16px;
                            height: 26px;
                            line-height: 26px;
                            color: #acaab6;
                            margin-top: 10px;
                        }

                        .mainFontdesc {
                            font-size: 16px;
                            height: 37px;
                            color: #acaab6;
                            margin-top: 10px;
                            line-height: 37px;
                        }
                    }
                }
            }
        }

        .useSpecification {
            .useSpecification-tips {
                height: 10%;
                /*border: 1px solid;*/
                width: 100%;

                .useSpecification-tips-left {
                    height: 100%;
                    float: left;
                    font-weight: 600;
                    line-height: 24px;
                }

                .useSpecification-tips-right {
                    float: left;
                    line-height: 24px;
                    height: 100%;
                    font-weight: 600;
                }
            }

            height: 60%;
            width: 100%;
            margin-left: 5px;
            margin-top: 10px;
        }
    }
</style>
<style scoped>
    .backColor {
        background-color: #61B0ff;
    }

    .textSty {
        text-align: center;
        font-size: 14px;
        color: #ffffff;
    }

    .PingFang {
        font-family: PingFang SC;
    }

    .Source {
        font-family: Source Han Sanns;
    }

    .Microsoft {
        font-family: Microsoft YaHei;
    }

    .HelveticaNeue {
        font-family: Helvetica Neue;
    }

    .Helvetica {
        font-family: Helvetica;
    }

    .Arial {
        font-family: Arial;
    }
</style>



