<template>
    <div id="Collapses">
        <div class="font-20"></div>
        <div>
            <div class="content">
                <div></div>
                <div>
                    <Collapse v-model="value1" accordion>
                        <Panel name="1">
                            
                            <p slot="content"></p>
                        </Panel>
                        <Panel name="2">
                            
                            <p slot="content"></p>
                        </Panel>
                        <Panel name="3">
                            
                            <p slot="content">
                                </p>
                        </Panel>
                    </Collapse>
                </div>
            </div>
            <div class="content">
                <div></div>
                <div>
                    <Collapse v-model="value1" simple accordion>
                        <Panel name="1">
                            
                            <p slot="content">Steve Jobs1955224</p>
                        </Panel>
                        <Panel name="2">
                            
                            <p slot="content">Stephen Gary
                                WozniakUC
                                BerkeleyEECS1987</p>
                        </Panel>
                        <Panel name="3">
                            
                            <p slot="content">
                                AppleiPodiMaciPhoneiPad</p>
                        </Panel>
                    </Collapse>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Collapses',
        data() {
            return {
                value1: '1'
            }
        }
    }
</script>

<style scoped lang="scss">
    #Collapses {
        height: 100%;
        overflow-y: auto;

        > > div:last-child {
            height: calc(100% - 50px);
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            > > div:first-child {
                background-color: #eee;
            }

            .content {
                height: 50%;
                padding: 8px 16px 16px;

                > > div:first-child {
                    margin-bottom: 4px;
                }

                > > div:last-child {
                    display: flex;
                    justify-content: space-between;

                    > > div {
                        width: 45%;
                    }
                }
            }
        }
    }
</style>



